export default {
	components: {},
	data() {
		return {
			code : '',
			balance: '0.00',
			record:[]
		};
	},
	onLoad(option) {
		console.log(option)
		let _this = this;
		this.TowerSwiper('swiperList');
		_this.getPageData(option);
	},
	methods: {
		getPageData(){
			var _this = this;
			let data = {
				'coupons_code' : _this.code
			};
			_this.$http.post('/api/card/getAmount', data).then(res => {
				console.log(res)
				if (res.data.code != 1) {
					uni.showToast({
						title: res.data.info,
						icon: 'none'
					});
				} else {
					var data = res.data.data
					_this.balance = data.amount
					_this.record = data.record
				}
			}).catch(err => {
				console.log(err);
			})
		},
		top_up(){
			uni.navigateTo({
				url: '/pages/money/card_pay'
			})
		},
		getCode(e){
			this.code = e.detail.value
		},
		//立即报名
		commit(e) {
			// console.log(e)
			var _this = this;
			let data = {
				'coupons_code' : _this.code
			};
			_this.$http.post('/api/coupons/get', data).then(res => {
				console.log(res)
				if (res.data.code != 1) {
					uni.showToast({
						title: res.data.info,
						icon: 'none'
					});
				} else {
					var orderId = res.data.data
					uni.showToast({
						title: res.data.info,
						icon: 'success'
					});
					setTimeout(function() {
						uni.redirectTo({
							url: '/pages/coupons/list'
						})
					}, 2000)
				}
			}).catch(err => {
				console.log(err);
			})
		}
		}
}
